html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  overflow: hidden;
  font-family: 'Roboto', sans-serif;
  font-size: 14px; }

.Board {
  display: flex;
  overflow-x: scroll;
  height: 100%;
  background: #ECF0F5;
  overflow-y: hidden;

  @at-root {}
  &__list {
    width: 260px;
    flex: none;
    display: flex;
    flex-direction: column;

    &--new-list {} }
  //

  &__list__header {
    box-sizing: border-box;
    padding: 10px;
    width: 100%;
    text-align: center;
    font-weight: bold; }

  &__list__body {
    overflow-y: scroll; }

  &__cardTitle {
    box-sizing: border-box;
    height: 40px;
    padding: 19px 5px 7px;
    font-weight: bold;
    font-size: 80%; }

  &__card, &__card--placeholder {
    margin: 0 4px 0px;
    padding: 5px; }

  &__card {
    border-width: 0 1px 1px 1px;
    border-style: solid;
    border-color: rgba(213, 220, 231, 0.43);
    background: #fff;
    color: #525252;
    line-height: 18px;
    position: relative;
    font-size: 85%;
    font-weight: bold;

    &:first-child {
      border-top: solid 1px rgba(213, 220, 231, 0.43); }

    &__info {
      margin-top: 7px;

      &__due {
        border: solid 1px #1d8824;
        display: inline-block;
        box-sizing: border-box;
        border-radius: 2px;
        color: #1d8824;
        font-size: 80%;
        font-weight: bold;
        padding: 4px 3px;
        text-align: center;
        line-height: 1;
        vertical-align: bottom; }

      &__member {
        display: inline-block;

        img {
          height: 20px;
          width: 20px;
          vertical-align: bottom;
          border-radius: 2px; } } } }


  &__card--placeholder {}
  //

  &:last-child {
    margin-bottom: 0; } }
